*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #689fe6;
  font-family: 'Montserrat',
  sans-serif;
  font-size: 25px;
  color: white;
  text-align: center;
}
.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.App > * {
  margin: 10px 0;
}
.app-info{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.app-info > * {
  margin: 0 30px;
}
.timezone{
  width: 200px;
  height: 40px;
  font-size: 25px;
}
.icon{
  width: 300px;
  height: 150px;
}
button{
  width: 150px;
  height: 30px;
  background: none;
  border: 2px solid #fff;
  color: white;
  font-size: 21px;
  cursor: pointer;
  transition: all .4s ease-in-out;
}
button:hover{
  background: #ffffff79;
}
@media screen and (max-width: 730px) {
  .app-info{
    flex-direction: column;
  }
  .App > * {
    margin: 0;
  }
}